html, body {
background-color: #e7dfd8;
margin-left:0;
margin-right:0;
min-height: 400px;
overflow: auto;
}

.logo {
  height: 6rem;
  width: 6rem;
  position: absolute;
  top: 3rem;
  border-radius: 50%
}

nav {
  display: flex;
  justify-content: space-evenly;
  margin-top: 5rem;
  height: 5rem
}

.nav-link {
  color: #231f20;
  text-decoration: none;
  font-family: Agrandir;
  font-weight: bold;
  max-height: 1rem;
}

.nav-link:hover {
  border-bottom: solid 2px #231f20;
  padding-bottom: 10px;


}

.top-spacer {
  height: 10rem;
}

.homePageContainer {
  height: 30rem;
  width: 70rem;
  border: solid 2px #231f20;
  margin-left: auto;
  margin-right: auto;
  align-self: center;
  display: flex;
  justify-content: space-between;
  padding: 4rem;
  border-radius: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.homePageHeader {
  font-size: 5rem;
  color: #231f20;
  font-family: Agrandir;
  margin-bottom: 0;
  margin-top: 0;
  margin-left: 5px;
}

.homePageSubheader {
  font-size: 1.5rem;
  color: #231f20;
  font-family: Agrandir;
  margin-bottom: 0;
  margin-top: 2px;
  margin-left: 5px;
}

.homeRight {
  object-fit: contain;
}

.headShot {
  width: 34rem;
  height: 30rem;
  object-fit: contain;
}

.svgBlock {
  margin-top:15rem
}

.belowTheFold {
  width: 100vw;
  background-color: #e7dfd8;
  display: flex;
  justify-content: space-around;
  gap: 1rem;
  min-height: max-content;
}

.projectTitle {
  color: #231f20;
  font-size: 2rem;
  margin-bottom: 0;
  margin-left: 3rem;
}

.projectDescription {
  color: #231f20;
  font-size: 1rem;
  margin-left: 3rem;
  margin-top: 0;
  font-style:italic;
}

.recentProjects {
  color: #231f20;
  font-size: 3rem;
  margin-left: 5rem;
  margin-top: 10rem
}

.footer {
  margin-top:3rem;
  height: 2rem;
  color: white;
  background-color: #183622;
  font-size: 15px;
  text-align: center;
  padding: 1rem;

}

.svg {
  padding: 1rem;
}

.svg:hover {
  fill:red;
}

.projectDisplayHome {
  width: 28rem;
  height: 23rem;
  border: solid 2px #231f20;
  border-radius: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.see-more-projects {
  border: 3px solid #183622;
  color: #183622;
  background-color: transparent;
  border-radius: 25px;
  margin: auto;
  height: 4rem;
  width: 13rem;
  font-size: 1.5rem;
  font-weight: bold;
  font-family: Agrandir;
}

.see-more-btn-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.see-more-projects:hover {
  cursor:pointer;
}

/* ABOUT PAGE  */

.aboutPageContainer {
  height: 60rem;
  width: 70rem;
  border: solid 2px #231f20;
  margin-left: auto;
  margin-right: auto;
  align-self: center;
  padding: 4rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 25px;
}

.aboutImageWrapper {
  object-fit: contain;
  display: flex;
  justify-content:center;
  width: 70rem;
  height: 16rem;
}

.aboutImage {
  width: 10rem;
  height: 15rem;
  object-fit: contain;
  margin-left: auto;
  margin-right: auto
}

.aboutPageHeader {
  font-size: 3rem;
  color: #231f20;
  font-family: Agrandir;
  margin-bottom: 0;
  margin-top: 0;
  text-align: center

}

.aboutMeBioBox {
  height: 40rem;
  width: 35rem;
  margin-left: auto;
  margin-right: auto
}

.aboutMeBio {
  color: #231f20;
  font-size: 1.5rem;
  font-family: Agrandir;
}

/* PROJECT PAGE  */

.projectPageDiv {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1rem
}

/* BLOG PAGE  */

.blogPageContainer {
  height: 60rem;
  width: 70rem;
  border: solid 2px #231f20;
  margin-left: auto;
  margin-right: auto;
  padding: 4rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-self: center;
}

.blogHeader {
  color: #231f20;
  font-size: 2rem;
  font-family: Agrandir;
  margin-bottom: 0;
  margin-top: 0;
}

.blogTeaserText {
  color: #231f20;
  font-size: 1.5rem;
  font-family: Agrandir;
  margin: 0
}

.blogReadMore {
  color: #231f20;
  font-size: 1.5rem;
  font-family: Agrandir;
  margin: 0
}

.blog-question {
  font-weight: bold;
  font-size: 1.2rem;
}

.blog-text {
  font-size: 1rem;
}

.blog-header {
  font-size: 2rem;
  font-weight: bold;
  font-style: italic;
  text-align: center;
  margin-top: 2rem;
}

.blog-date {
  font-size: 1rem;
  margin:0;
}

@media only screen and (max-width: 600px) {
  html, body {
    overflow-x: hidden;
  }
  body {
    position: relative
  }
  nav {
    height:3rem;
  }
  .logo {
    height: 4rem;
    width: 4rem;
    top: 0.5rem;
  }
  .homePageContainer {
    height: 25rem;
    width:14rem;
    justify-content: center;
    gap:4rem;
    padding: 3rem;
    flex-direction:column;

  }
  .homePageHeader {
    font-size:2rem;
  }
  .svgBlock {
    margin-top: 1rem;
  }
  .svg {
    padding: 0.5rem;
  }
  .svg img {
    width: 30px;
    height: 30px;
  }
  .homePageSubheader {
    font-size: 1rem;
  }
  .homeRight {
    display: flex;
    justify-content: center;
  }
  .headShot {
    width:12rem;
    height: 13rem;
  }
  .recentProjects {
    margin-top:5rem;
    font-size:2rem;
  }
  .belowTheFold {
    flex-direction: column;
  }
  .projectDisplayHome {
    width:23rem;
    height: 20rem;
    margin-left: 8px;
  }
  .projectTitle {
    margin-top: 1rem;
    font-size: 1rem;
  }
  .aboutPageContainer {
    height: 39rem;
    width: 14rem;
    padding: 3rem;
  }
  .aboutImageWrapper {
    width: 14rem;
    height: 12rem;
  }
  .aboutImage {
    width: 8rem;
    height: 11rem;
  }
  .aboutMeBio {
    font-size: 0.75rem
  }
  .aboutMeBioBox {
    width: 16rem;
  }
  .aboutPageHeader {
    font-size: 2rem;
  }
  .blogPageContainer {
    width: 14rem;
    height: max-content;
    padding: 3rem;
    gap: 1rem;
  }
  .blogHeader {
    font-size: 1.5rem;
  }
  .blogTeaserText {
    font-size: 1rem;
  }
}
